.burger-button {
	//.burger-button__wrapper
	&__wrapper {
		cursor: pointer;
		padding: 10px 6px;
		height: 36px;
		width: 36px;
		overflow: hidden;
		pointer-events: none;

		backdrop-filter: blur(10px);
		// box-shadow: 0 2px 9px #6c8fac;
	}

	&__items {
		border-radius: 5px;
		position: relative;

		span {
			opacity: 1;
			left: 0;
			display: block;
			width: 100%;
			height: 2px;
			border-radius: 2px;
			background-color: #171b1c;
			position: absolute;
			transform: rotate(0deg);
			transition: 0.7s ease-in-out;

			&:nth-child(2) {
				top: 7px;
			}
			&:nth-child(3) {
				top: 14px;
			}
		}
	}

	&._open-menu {
		span {
			&:nth-child(1) {
				transform: scale(1.088) translateY(7px) rotate(135deg);
			}
			&:nth-child(2) {
				opacity: 0;
				transform: translateX(-60px);
			}
			&:nth-child(3) {
				transform: scale(1.088) translateY(-6px) rotate(-135deg);
			}
		}
	}
}
