@media (min-width: 1440.99px) {
  .underline:hover, .menu-list__link:hover, .header__phone:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before, .menu-list__link:hover::before, .header__phone:hover::before {
    width: 75%;
  }
  .underline:hover::after, .menu-list__link:hover::after, .header__phone:hover::after {
    width: 40%;
  }
  .underline::before, .sidebar-menu .menu-list__link::before, .menu-list__link::before, .header__phone::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    width: 0;
    height: 2px;
    background-color: #2b3769;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after, .sidebar-menu .menu-list__link::after, .menu-list__link::after, .header__phone::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0;
    height: 2px;
    background-color: #2b3769;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
