:root {
  --clip: left center;
  --size: 0;
}

.button {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px;
  border-radius: 3rem;
  white-space: nowrap;
  max-width: 266px;
  max-height: 65px;
  overflow: hidden;
}
.button ._icon {
  width: 4.8rem;
  height: 4.8rem;
  transition: color 0.5s, transform 0.5s ease-in-out;
}
.button ._icon::before {
  font-size: 2.2rem;
}
.button .icon {
  z-index: 2;
  transition: background 0.9s, transform 0.5s ease-in-out;
}
.button span {
  user-select: none;
  pointer-events: none;
  transition: color 0.5s ease-in-out;
  padding: 0.7rem 2rem 1.3rem 3rem;
  width: 100%;
  font-size: 2rem;
  font-weight: 500;
  line-height: 125%; /* 25px/20 */
  text-align: center;
  z-index: 2;
}
.button::before {
  content: "";
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  clip-path: circle(var(--size) at var(--clip));
  transition: 0.5s ease-in-out;
}
.button:hover:before {
  --size: 200%;
}
.button:hover span {
  color: #fff;
}
.button:hover ._icon::before {
  font-weight: 600;
  color: #2b3769;
}
.button:hover .icon {
  background: #fff;
}

