.sidebar-menu {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: calc(100% - 50px);
  z-index: 10;
  transform: translateY(-120%);
  will-change: transform;
  display: flex;
  backdrop-filter: blur(7px);
  background-image: radial-gradient(at 68.8718140104% 24.1377728129%, hsl(236.4705882353, 37.7777777778%, 44.1176470588%) 0%, hsla(236.4705882353, 37.7777777778%, 44.1176470588%, 0) 100%), radial-gradient(at 10.6543058744% 44.3831249585%, hsl(206.5573770492, 42.6573426573%, 28.0392156863%) 0%, hsla(206.5573770492, 42.6573426573%, 28.0392156863%, 0) 100%), radial-gradient(at 88.7266315117% 7.847963994%, hsl(228.3870967742, 41.8918918919%, 29.0196078431%) 0%, hsla(228.3870967742, 41.8918918919%, 29.0196078431%, 0) 100%), radial-gradient(at 17.3732184931% 58.3725132531%, hsl(222.8571428571, 19.1780821918%, 28.6274509804%) 0%, hsla(222.8571428571, 19.1780821918%, 28.6274509804%, 0) 100%), radial-gradient(at 26.3339831457% 71.5811896497%, hsl(0, 0%, 16.0784313725%) 0%, hsla(0, 0%, 16.0784313725%, 0) 100%), radial-gradient(at 18.0534742028% 33.0296412444%, hsl(236.4705882353, 37.7777777778%, 44.1176470588%) 0%, hsla(236.4705882353, 37.7777777778%, 44.1176470588%, 0) 100%);
}
.sidebar-menu::before {
  content: "";
  position: fixed;
  top: 100%;
  left: 0;
  width: 100%;
  height: 0;
  transform: translateY(-50%);
  transition: height 0.4s ease-out 0.7s;
}
.sidebar-menu__column {
  opacity: 0;
  width: 100%;
  overflow-x: hidden;
  min-width: 300px;
}
@media (min-width: 960.99px) {
  .sidebar-menu__column {
    flex: 0 1 40%;
  }
}
@media screen and (max-width: 490px) {
  .sidebar-menu__column {
    width: 100%;
  }
}
.sidebar-menu__column:nth-child(2) {
  width: 100%;
  display: flex;
  justify-content: center;
  flex: 0 1 auto;
  overflow: hidden;
}
@media screen and (max-width: 960px) {
  .sidebar-menu__column:nth-child(2) {
    display: none;
  }
}
.sidebar-menu__menu {
  width: 100%;
  position: relative;
  z-index: 15;
  padding: 30px 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar-menu__menu .header__menu {
  flex-direction: column;
}
.sidebar-menu .menu-list {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
}
.sidebar-menu .menu-list__link {
  position: relative;
  text-transform: uppercase;
  align-self: start;
  font-size: 18px;
  font-weight: 600;
  opacity: 1;
  white-space: nowrap;
  font-family: "Inter", sans-serif;
  color: #171b1c;
  opacity: 0.7;
  transition: all 0.5s ease-in-out;
}
.sidebar-menu .menu-list__link:not(:last-child) {
  margin-bottom: 20px;
}
.sidebar-menu .menu-list__link:hover {
  opacity: 1;
}
.sidebar-menu__order {
  position: relative;
}
.sidebar-menu__order::before {
  content: "";
  margin-bottom: 40px;
  display: block;
  height: 2px;
  width: 100%;
  opacity: 0;
  background: #aaaaaa;
  box-shadow: inset 0 0 2px #dbdbdb;
}
.sidebar-menu__order::after {
  content: "";
  margin-top: 40px;
  display: block;
  height: 2px;
  width: 100%;
  opacity: 0;
  background: #aaaaaa;
  box-shadow: inset 0 0 2px #dbdbdb;
}
.sidebar-menu__project-button {
  text-transform: uppercase;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: #171b1c;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sidebar-menu__button {
  cursor: pointer;
  position: relative;
  margin-right: 30px;
  width: 7.8rem;
  height: 7.8rem;
  border-radius: 50%;
  border: 2px solid #ccc;
  opacity: 0;
  overflow: hidden;
}
.sidebar-menu__button .icon-arrow-right {
  position: relative;
  z-index: 9;
  color: #ccc;
  font-size: 24px;
  transition: 0.5s linear;
  transition: all 0.4s ease-in-out;
}
.sidebar-menu__button:before {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #2b3769;
  clip-path: circle(var(--size) at var(--clip));
  transition: all 0.7s ease-in-out;
}
.sidebar-menu__button:hover .icon-arrow-right {
  z-index: 9;
  transform: rotate(-45deg);
}
.sidebar-menu__button:hover:before {
  --size: 100px;
}
.sidebar-menu__row {
  display: flex;
  gap: 15px;
  justify-content: space-between;
  align-items: flex-end;
  padding: 40px 0 0;
  width: 100%;
}
@media screen and (max-width: 490px) {
  .sidebar-menu__row {
    align-items: flex-start;
    flex-direction: column-reverse;
    gap: 40px;
  }
}
.sidebar-menu__slide {
  position: relative;
  z-index: 999;
  height: 100%;
  display: flex;
  gap: 30px;
  background-size: cover;
  background-position: center;
}
.sidebar-menu._open-menu {
  transform: translateY(0px);
}
.sidebar-menu._open-menu .sidebar-menu__column {
  opacity: 1;
  transition: opacity 0.7s ease-in-out 0.9s;
}
.sidebar-menu._open-menu::before {
  height: 200%;
  background: #fff;
}
.sidebar-menu._open-menu .sidebar-menu__order::before {
  opacity: 1;
  transition: opacity 0.7s ease-in-out 0.9s;
  will-change: opacity;
}
.sidebar-menu._open-menu .sidebar-menu__order::after {
  opacity: 1;
  transition: opacity 0.7s ease-in-out 0.9s;
  will-change: opacity;
}
.sidebar-menu._open-menu .menu-list__link {
  color: #171b1c;
  transition: color 0.9s ease-in-out 0.7s;
  will-change: color;
}
.sidebar-menu._open-menu .sidebar-menu__button {
  opacity: 1;
  transition: opacity 0.7s ease-in-out 0.7s;
  will-change: opacity;
}
.sidebar-menu._open-menu .main-wrapper {
  opacity: 1;
  transition: opacity 0.7s ease-in-out 0.9s;
  will-change: opacity;
}
.sidebar-menu._close-menu {
  transition: height 1s ease-out 0s;
}
.sidebar-menu._close-menu .sidebar-menu__column {
  opacity: 0;
  transition: opacity 0.3s ease-in-out 0.7s;
}
.sidebar-menu._close-menu .main-wrapper {
  opacity: 0;
  transition: opacity 0.3s ease-in-out 0.7s;
  will-change: opacity;
}
.sidebar-menu._close-menu .sidebar-menu__order::before {
  opacity: 0;
  transition: opacity 0.2s ease-in-out 0.7s;
  will-change: opacity;
}
.sidebar-menu._close-menu .sidebar-menu__order::after {
  opacity: 0;
  transition: opacity 0.2s ease-in-out 0.7s;
  will-change: opacity;
}
.sidebar-menu._close-menu .menu-list__link {
  color: rgba(255, 255, 255, 0);
  transition: color 0.9s ease-in-out 0.7s;
  will-change: color;
}
.sidebar-menu._close-menu .sidebar-menu__button {
  opacity: 0;
  transition: opacity 0.7s ease-in-out 0.2s;
  will-change: opacity;
}
.sidebar-menu._close-menu::before {
  height: 0;
}

